import * as React from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Divider from "@mui/material/Divider";
import ListItemText from "@mui/material/ListItemText";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import Avatar from "@mui/material/Avatar";
import Typography from "@mui/material/Typography";
import Chip from "@mui/material/Chip";

export default function AlignItemsList() {
	const data = [
		{
			id: 1,
			title: "Clobber Axe",
			description: "-20%%",
			icon: "",
			action: "365 ETH",
		},
		{
			id: 2,
			title: "Clobber Axe",
			description: "-20%%",
			icon: "",
			action: "365 ETH",
		},
		{
			id: 3,
			title: "Clobber Axe",
			description: "-20%%",
			icon: "",
			action: "365 ETH",
		},
		{
			id: 4,
			title: "Clobber Axe",
			description: "-20%%",
			icon: "",
			action: "365 ETH",
		},
		{
			id: 5,
			title: "Clobber Axe",
			description: "-20%%",
			icon: "",
			action: "365 ETH",
		},
	];
	return (
        <>
            <div className="List">
			<List style={{borderRadius:"11px"}} sx={{ width: "100%", maxWidth: 360, bgcolor: "background.paper" }}>
				{data.map((item) => (
					<><div className="cardList">
						<ListItem alignItems="flex-start" sx={{padding:'0px',borderRadius:'15px',backgroundColor:'#ECF0F9'}}>
							<ListItemAvatar>
								<Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" />
							</ListItemAvatar>
							<ListItemText
								primary={item.title}
								secondary={
									<React.Fragment>
										<Chip
											size="small"
											color="primary"
											label={item.description}
										/>
									</React.Fragment>
								}
							/>
							<h5 style={{marginRight:'3%'}}>&#11045;{item.action}</h5>
						</ListItem></div>
					
					</>
				))}
                </List>
                </div>
		</>
	);
}
